import React,{useEffect,useState} from 'react';
import {Navbar,Nav,Button,Form,Container,FormControl} from 'react-bootstrap'
import {NavLink} from 'react-router-dom';
import {auth} from '../firebase/init'
import { onAuthStateChanged } from 'firebase/auth';
import {useNavigate} from 'react-router-dom'

export default function HomeNav() {
    const [user, setUser] = useState(null);
    const nav=useNavigate()
    useEffect(() =>{
        onAuthStateChanged(auth,user=>{
            if(user){
                setUser(user)
            }
            else{
                nav('/signup',{replace:true})
            }
        })
    },[user])


  return (
    <Navbar bg="dark" variant='dark' fixed='top' expand="md">
  <Container fluid>
    <Navbar.Brand className='text-danger' href="/"><span style={{fontWeight:700,fontSize:32}} >iuMovie</span></Navbar.Brand>
    <Form className="d-flex">
        <FormControl
          type="search"
          placeholder="Search"
          className="me-2"
          aria-label="Search"
        />
        <Button variant="outline-danger">Search</Button>
      </Form>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav" className='justify-content-between'>
    <Nav className='justify-content-center flex-grow-1'>
        <NavLink to='/'  className={({isActive})=>{
        return isActive? 'border-bottom border-danger opacity-100 text-light nav-link':'nav-link'
        }}>Home</NavLink>
         <NavLink to='/list' className={({isActive})=>{
        return isActive? 'border-bottom text-light border-danger opacity-100  nav-link':'nav-link'
        }}>List</NavLink>
        <NavLink to='/about' className={({isActive})=>{
        return isActive? 'border-bottom text-light border-danger opacity-100  nav-link':'nav-link'
        }}>About</NavLink>
        <NavLink to='/contact' className={({isActive})=>{
        return isActive? 'border-bottom text-light border-danger opacity-100  nav-link':'nav-link'
        }}>Contact</NavLink>
      </Nav>
      
     <div style={{marginLeft:16,paddingTop:8}}>
     {user && 
        <div className="d-flex justify-content-between  flex-grow-1 align-items-center">
        <img src={user.photoURL} alt={user.name}  width='64' height='64' className='img-responsive rounded-circle' />
        </div>
     }
     </div>
    </Navbar.Collapse>
  </Container>
</Navbar>
  );
}
