import React,{ useState,useEffect} from 'react';
import {ref,listAll,getDownloadURL,list} from 'firebase/storage'
import {storage} from '../firebase/init'
import {Container,Row,Col,Button,Carousel} from 'react-bootstrap'
import {useNavigate} from 'react-router-dom'


//pages functions

async function pageToken(){
    // Create a reference under which you want to list
    const listRef = ref(storage, 'movies');
  
    // Fetch the first page of 100.
    const firstPage = await list(listRef, { maxResults: 4 });
  
    // Use the result.
    processItems(firstPage.items)
    // processPrefixes(firstPage.prefixes)
  
    // Fetch the second page if there are more elements.
    if (firstPage.nextPageToken) {
      const secondPage = await list(listRef, {
        maxResults: 4,
        pageToken: firstPage.nextPageToken,
      });
      processItems(secondPage.items)
    //   processPrefixes(secondPage.prefixes)
    }
  }

  function processItems(items){
      console.log('items > ')
      console.log(items)
  }
  

  const moviesRef=ref(storage,'movies')

export default function MovieList() {

    const [nextList,setNextList]=useState(null);
    const [movielist, setMovielist] = useState([]);
    const [page, setPage] = useState(null);
    const nav=useNavigate()

    useEffect(() =>{
        const get=async ()=>{
          const first=await list(moviesRef,{maxResults:4})
          setPage(first)
          first.items.forEach(movieRef=>{
            getDownloadURL(movieRef).then(url=>{
              setMovielist(old=>[...old,url])
              console.log(movieRef)
            }).catch(err=>{console.log(err)})
          })
        }
        get()
    },[])

    const getNext=async ()=>{
      if (page.nextPageToken) {
        const next = await list(moviesRef, {
          maxResults: 4,
          pageToken: page.nextPageToken,
        });
        setPage(next)
        next.items.forEach(MovieRef=>{
          getDownloadURL(MovieRef).then(url=>{
            setMovielist(old=>[...old,url])
          }).catch(err=>{console.log(err)})
        })
    }
  }

  const goWatch=(url) => {
    nav('/watch',{replace :true,state:{url:url}})
  }

  return (
    <div >
      <Container fluid className>
      <Carousel>
    {movielist && movielist.map(url=>{
      return (
      <Carousel.Item style={{height:'50vh'}} onClick={()=>{
        goWatch(url)
      }} >
        <video
          className="d-block w-100 h-100"
          src={url}
          alt="First slide"

        />
        <Carousel.Caption>
          <h3>First slide label</h3>
          <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
        </Carousel.Caption>
      </Carousel.Item>
      )
    })}
    </Carousel>
    </Container>
    </div>
  );
}
