import React,{useEffect,useState} from 'react';
import {useLocation,useNavigate} from 'react-router-dom';
import {Button} from 'react-bootstrap'

export default function Movie() {
  const [videoURL, setVideoUrl] = useState(null)
  const location=useLocation()
  const nav=useNavigate()


  useEffect(() =>{
    const {url}=location.state || null
    if(url !==null){
      setVideoUrl(url)
    }
  },[])

  const goHome = ()=>{
    nav('/',{replace:true})
  }
  const goUser=()=>{
    nav('/user',{replace:true})
  }

  return (
    <div className='position-fixed top-0 start-0 end-0 bottom-0 width-100 h-100 bg-dark text-light' style={{zIndex:1}}>
      <div className="position-absolute top-50 start-50 w-75 translate-middle h-auto  ">
      {videoURL && <video src={videoURL} controls className='w-100 h-auto'></video>}
      </div>
      <div className="position-absolute bottom-0 start-0 w-100 d-flex justify-content-between align-items-center p-4">
        <Button variant='outline-danger' className='text-light mx-2 w-100' onClick={() =>{goHome()}} >Back Home</Button>
        <Button variant='outline-danger' className='text-light mx-2 w-100 ' onClick={() =>{goUser()}}>User</Button>
      </div>
    </div>
  );
}
