// Import FirebaseAuth and firebase.
import React, { useEffect, useState } from 'react';
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';
import firebase from 'firebase/compat/app';
import 'firebase/compat/auth';
import {auth} from '../firebase/init'
import {onAuthStateChanged} from 'firebase/auth'
import {useNavigate} from 'react-router-dom'

// Configure FirebaseUI.
const uiConfig = {
  // Popup signin flow rather than redirect flow.
  signInFlow: 'popup',
  // We will display Google and Facebook as auth providers.
  signInOptions: [
    firebase.auth.GoogleAuthProvider.PROVIDER_ID,
    firebase.auth.PhoneAuthProvider.PROVIDER_ID
  ],
  signInSuccessUrl: '/',
};

function Signup() {
  const [isSignedIn, setIsSignedIn] = useState(false); // Local signed-in state.
const nav=useNavigate()
  // Listen to the Firebase Auth state and set the local state.
  useEffect(() => {
    const unregisterAuthObserver = onAuthStateChanged(auth,user=>{
        if(user){
            nav('/',{replace:true})
        }
        else{
            setIsSignedIn(!!user)
        }
    })
    return () => unregisterAuthObserver(); // Make sure we un-register Firebase observers when the component unmounts.
  }, []);

  if (isSignedIn) {
    nav('/',{replace:true})
  }
  return (
    <div className=' ' >
      <div className='text-center rounded shadow-lg shadow-primary position-absolute top-50 start-50 translate-middle w-75 h-75 ' style={{maxWidth:568,padding:16}}>
      <div className="py-3"><h1 style={{fontWeight:'bold'}}>UIMOVIE</h1>
      <p style={{fontWeight:'bold'}} >Please sign-in:</p></div>
      <StyledFirebaseAuth uiConfig={uiConfig} firebaseAuth={auth} />
      </div>
    </div>
  );
}

export default Signup;
